.icon {
  margin : 2%;
  display: inline-flex;
}

.icons {
  text-align: right;
  position  : absolute;
  bottom    : 3%;
  right     : 3%;
  width     : 10%;
}

@media (max-width: 1200px) {
  html {
    font-size: 10pt;
  }
}

@media (max-width: 680px) {
  html {
    font-size: 8pt;
  }
}

